export default {
	data() {
		return {
			schoolInfo:null,
			pageNum:1
		}
	},
	methods: {
		loadData()
		{
			var me = this
			uni.request({
				url: this.serverUrl + '/led/school/' + this.pageNum + '?name=&limit=1',
				method: 'GET',
				data: {},
				success: res => {
					me.schoolInfo = res.data[0]
				},
				fail: () => {},
				complete: () => {}
			});
		},
		prevent()
		{
			if(this.pageNum>1)
			{
				this.pageNum--
				this.loadData()
			}
		},
		next()
		{
			this.pageNum++
			this.loadData()
		},
		showLedgerAndTeacherInfo()
		{
			uni.navigateTo({
				url: "../ledger_school_detail/ledger_school_detail?schoolId=" + this.schoolInfo.id
			})
		}
	},
	onLoad() {
		this.loadData()
	}
}
